<template>
  <div>
    <el-card class="login-form-layout">
      <el-form
        autocomplete="on"
        :model="loginForm"
        :rules="loginRules"
        ref="loginForm"
        label-position="left"
      >
        <div style="text-align: center">
          <img :src="LogoSrc" alt="" style="height: 80px;">
        </div>
        <h2 class="login-title color-main">供应商</h2>
        <el-form-item prop="username">
          <el-input
            name="username"
            type="text"
            v-model="loginForm.username"
            autocomplete="on"
            placeholder="请输入用户名"
          >
            <span slot="prefix">
              <svg-icon icon-class="user" class="color-main"></svg-icon>
            </span>
          </el-input>
        </el-form-item>
        <el-form-item prop="password">
          <el-input
            name="password"
            :type="pwdType"
            @keyup.enter.native="handleLogin"
            v-model="loginForm.password"
            autocomplete="on"
            placeholder="请输入密码"
          >
            <span slot="prefix">
              <svg-icon icon-class="password" class="color-main"></svg-icon>
            </span>
            <span slot="suffix" @click="showPwd">
              <svg-icon icon-class="eye" class="color-main"></svg-icon>
            </span>
          </el-input>
        </el-form-item>
        <el-form-item style="margin-bottom: 60px">
          <div>
            <el-button
              style="width: 100%"
              type="primary"
              :loading="loading"
              @click.native.prevent="handleLogin"
            >登录</el-button>
          </div>
          <div style="margin-top:10px">
            <el-button style="width: 100%" type="info" @click.native.prevent="handleRegister">注册</el-button>
          </div>
          <div class="BothEqual">
            <el-checkbox v-model="checked">记住密码</el-checkbox>
            <el-button type="text" @click.native.prevent="handleretrieve">找回密码</el-button>
          </div>
        </el-form-item>
      </el-form>
    </el-card>
    <img :src="login_center_bg" class="login-center-layout">
  </div>
</template>

<script>
import { isvalidUsername } from "@/utils/validate";
// import {setSupport,getSupport,SupportUrl} from '@/utils/support';
import login_center_bg from "@/assets/images/login_center_bg.png";
import {mapMutations} from 'vuex';
export default {
  name: "login",
  data() {
    const validateUsername = (rule, value, callback) => {
      if (!isvalidUsername(value)) {
        callback(new Error("请输入正确的用户名"));
      } else {
        callback();
      }
    };
    const validatePass = (rule, value, callback) => {
      if (value.length < 3) {
        callback(new Error("密码不能小于3位"));
      } else {
        callback();
      }
    };
    return {
      loginForm: {
        username: "",
        password: ""
      },
      loginRules: {
        username: [
          { required: true, trigger: "blur", validator: validateUsername }
        ],
        password: [{ required: true, trigger: "blur", validator: validatePass }]
      },
      loading: false,
      pwdType: "password",
      login_center_bg,
      LogoSrc:"",
    };
  },
  computed:{
    checked:{
      get(){
        return this.$store.getters.isRememberpwd;
      },
      set(value){
          this.SET_isRememberpwd(value);
      }
    }
  },
  created() {
    if(localStorage.getItem('suplieName')){
      this.loginForm.username=localStorage.getItem('suplieName');
    }
     if(localStorage.getItem('supliepwd')){
      this.loginForm.password=localStorage.getItem('supliepwd');
    }
    sessionStorage.removeItem("regStatus");
    this.$request.GetSysConfig().then(res=>{
      localStorage.setItem('SUPPLYGetlocation',JSON.stringify(res.content.SupplierDomain.Value));
      this.LogoSrc=res.content.HeadQuartersDomain.Value+res.content.Logo.Value;
    })
  },
  methods: {
    ...mapMutations(['SET_isRememberpwd']),
    showPwd() {
      if (this.pwdType === "password") {
        this.pwdType = "";
      } else {
        this.pwdType = "password";
      }
    },
    handleLogin() {
      this.$refs.loginForm.validate(async valid => {
        if (valid) {
          this.loading = true;
          let res = await this.$store.dispatch("Login", this.loginForm);
          if (res.code==0) {
            this.$Message.success("登陆成功");
            this.$router.push({ path: "/" });
          } else if(res.code==1){
            this.$Message.error(res.msg);
          }
          this.loading = false;
        } else {
          this.$Message.error("请填写正确的用户名或密码");
        }
      });
    },
    handleRegister() {
      sessionStorage.setItem("regStatus", 0);
      this.$router.push({ path: "/register" });
    },
    handleretrieve() {
      sessionStorage.setItem("regStatus", 1);
      this.$router.push({ path: "/register" });
    }
  }
};
</script>

<style scoped>
.login-form-layout {
  position: absolute;
  left: 0;
  right: 0;
  width: 360px;
  margin: 140px auto;
  border-top: 10px solid #409eff;
}

.login-title {
  text-align: center;
}

.login-center-layout {
  background: #409eff;
  width: auto;
  height: auto;
  max-width: 100%;
  max-height: 100%;
  margin-top: 200px;
}
.BothEqual{
   display:-webkit-box;
    display: -moz-box;
    display: -ms-flexbox;
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content:space-between;
justify-content:space-between;
-moz-box-pack:space-between;
-webkit--moz-box-pack:space-between;
box-pack:space-between;
}
</style>



